import React from "react";
import axios from "axios";
import Movie from "../components/Movie";
import style from "../css/Home.module.css";

function toTop(){
    window.scroll({top:0, left:0, behavior:"smooth"});
}

class Home extends React.Component{ // React v5

  state = {
    isLoading: true,
    movieArr: []
  };

  getMovies = async () => {
    const movies = await axios.get("https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=rating");
    this.setState({
      movieArr: movies.data.data.movies,
      isLoading: false,
    });
  }


  componentDidMount(){
    this.getMovies();
  }

  render(){
    const { isLoading, movieArr } = this.state;
    return (
      <section className={style.wrap}>
        <h1 onClick={toTop} className={style.title}>Movie List</h1>
        {isLoading 
        ? <div className={`${style.loader} ${style.center}`}>Loading...</div> 
        : <div className={`${style.movie_list} ${style.center}`}>
            {movieArr.map((item) => {
              return <Movie 
                id={item.id}
                key={item.id} 
                title={item.title} 
                year={item.year} 
                summary={item.summary.length > 200 ? `${item.summary.slice(0, 200)}...` : item.summary} 
                poster={item.medium_cover_image}
                genres={item.genres}
              />
            })}
          </div>
        }
      </section>
    )
  }

}

export default Home;
