import React from "react";
import { Link } from "react-router-dom";
import propTypes from "prop-types";
import style from "../css/Movie.module.css";

function Movie({id, title, year, summary, poster, genres }){
    return (
        <div className={style.box_container}>
            <img src={poster} alt={title} title={title} />
            <div className={style.box_content}>
                <h3 className={style.title}>{title} <span className={style.year}>({year})</span></h3>
                <ul className={style.genre}>{genres.map((item, idx) => { return <li key={idx}>{item}.</li>})}</ul>
                <p className={style.summary}>{summary}</p>
                <p className={style.view}><Link to={process.env.PUBLIC_URL +`about/${id}`}>view details..</Link></p>
            </div>
        </div>
    )
}

Movie.prototype = {
    id:propTypes.string.isRequired,
    title: propTypes.string.isRequired,
    year: propTypes.number.isRequired,
    summary: propTypes.string.isRequired,
    poster: propTypes.string.isRequired,
    genres: propTypes.arrayOf(propTypes.string).isRequired
}

export default Movie;
