import React, { useEffect } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useState } from 'react/cjs/react.development';
import style from '../css/About.module.css';

function About(){
    const {id} = useParams();
    const [load, setLoad] = useState(true);
    const [detail, setDetail] = useState([]);
    
    const getMovie = async () => {
        const getMovieData = await (
            await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)
            .then((res) => res.json())
        );
        console.log(detail)
        setDetail(getMovieData.data.movie);
        setLoad(false);
    }

    useEffect(()=>{
        getMovie();
    }, []);  
    
    const {
        title, 
        large_cover_image, 
        description_intro,
        genres,
        rating,
        runtime
    } = detail;

    return (
        <section className={style.bg}>
            {load 
            ? <div className={style.loading}><p>loading...</p></div>
            : <div className={style.loaded}>
                <h2 className={style.title}>{title}</h2>
                <div className={style.image}>
                    <img src={large_cover_image} alt={title} title={title} />
                </div>
                <h3 className={style.genres}>Genres</h3>
                <ul className={style.genreList}>
                    {genres.map((g) => <li>{g}</li>)}
                </ul>
                <p className={style.rating}><span>Rating : {rating}</span><span>Runtime : {runtime}m</span></p>
                <p className={style.decription}>{description_intro}</p>
                <button><Link to={process.env.PUBLIC_URL + "/"}>Back</Link></button>
              </div> 
            }        
        </section>
    )
}

export default About;